"use client";
import { useState, useRef, useEffect } from "react";
import { LogOut } from "lucide-react";
import { useRouter } from "next/navigation";

type Props = {
  name?: string;
  email?: string;
};

export default function ProfileDropdown({ name, email }: Props) {
  const [open, setOpen] = useState(false);
  const ref = useRef<HTMLDivElement>(null);
  const router = useRouter();

  // Provide fallback values
  const displayName = name && name.trim() ? name : "User";
  const displayEmail = email && email.trim() ? email : "user@example.com";
  const displayInitial = displayName[0];

  useEffect(() => {
    function handleClick(e: MouseEvent) {
      if (ref.current && !ref.current.contains(e.target as Node)) setOpen(false);
    }
    if (open) document.addEventListener("mousedown", handleClick);
    return () => document.removeEventListener("mousedown", handleClick);
  }, [open]);

  return (
    <div className="relative" ref={ref}>
      <button
        className="flex items-center gap-2 px-3 py-2 rounded-full bg-white hover:bg-gray-100 border"
        onClick={() => setOpen((v) => !v)}
      >
        <span className="font-semibold">{displayInitial}</span>
        <svg width={18} height={18} fill="none" viewBox="0 0 18 18">
          <path d="M6 7l3 3 3-3" stroke="#25388C" strokeWidth={2} strokeLinecap="round" />
        </svg>
      </button>
      {open && (
        <div className="absolute right-0 bottom-12 mb-2 w-64 bg-white rounded-lg shadow-lg border z-50 animate-fade-in-up">
          <div className="p-4 border-b">
            <div className="font-bold">{displayName}</div>
            <div className="text-xs text-gray-500 truncate">{displayEmail}</div>
          </div>
          <button
            className="w-full text-left px-4 py-3 hover:bg-gray-50 flex items-center gap-2"
            onClick={() => {
              setOpen(false);
              router.push("/profile");
            }}
          >
            <span>üìù</span> Edit profile
          </button>
          <button
            className="w-full text-left px-4 py-3 hover:bg-gray-50 flex items-center gap-2"
            onClick={() => alert("Widget settings coming soon!")}
          >
            <span>‚öôÔ∏è</span> Widget settings
          </button>
          <button
            className="w-full text-left px-4 py-3 hover:bg-gray-50 flex items-center gap-2"
            onClick={() => alert("Upgrade coming soon!")}
          >
            <span>‚≠ê</span> Upgrade to professional
          </button>
          <button
            className="w-full text-left px-4 py-3 hover:bg-red-50 text-red-600 flex items-center gap-2 border-t"
            onClick={() => {
              localStorage.removeItem("token");
              router.push("/login");
            }}
          >
            <LogOut className="w-5 h-5" /> Logout
          </button>
        </div>
      )}
    </div>
  );
}